.App {
  text-align: center;
}

.calendar {
  width: 400px;
  margin: 20px auto;
  background-color: #e0d2d2d8;
  border-radius: 10px;
  padding: 20px;
}

.calendar-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}

.calendar-header button {
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
}

.calendar-header span {
  font-size: 18px;
  font-weight: bold;
}

.calendar-header select {
  padding: 5px;
}

.calendar-days {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  font-weight: bold;
  color: #555;
}

.calendar-cells {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 5px;
}

.calendar-cell {
  padding: 10px;
  text-align: center;
  cursor: pointer;
  border-radius: 5px;
  background-color: #fff;
}

.calendar-cell:hover {
  background-color: #ececec;
}

.today {
  background-color: #d1e7dd;
}

.selected {
  background-color: #0d6efd;
  color: #fff;
}
