.calendar {
    max-width: 400px;
    padding: 1rem;
    background-color: #f5f5f5;
    border-radius: 12px;
    font-family: Arial, sans-serif;
  }
  
  .calendar-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 1rem;
  }
  
  .month-navigation {
    display: flex;
    align-items: center;
    gap: 1rem;
  }
  
  .month-navigation button {
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
  }
  
  .week-days, .days {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
  }
  
  .week-day {
    text-align: center;
    font-weight: bold;
    padding: 0.5rem;
  }
  
  .day, .empty-day {
    text-align: center;
    padding: 1rem;
    cursor: pointer;
  }
  
  .today {
    background-color: #e0e0e0;
    border-radius: 50%;
  }
  
  .selected {
    background-color: #6a5acd;
    color: white;
    border-radius: 50%;
  }
  